* {
  margin: 0;
  padding: 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 24px;
  padding-top: 70px;
  padding-left: 24px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 24px);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  border: 2px solid rgba(200, 200, 206, .757);
  border-top: none;
  justify-content: space-around;
  display: flex;
}

.info-name {
  font-weight: 600;
}

.info-value {
  font-weight: 500;
}

.info-desc {
  flex-direction: column;
  align-items: center;
  display: flex;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

button.hidden {
  display: none;
}

.search-form {
  width: 100%;
  background-color: #404bbf;
  justify-content: center;
  display: flex;
  position: fixed;
  top: 0;
}

.search-form input {
  width: 300px;
  margin: 2px;
  padding: 10px;
  font-size: 18px;
}

.search-form button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  font-size: 23px;
  position: absolute;
  top: 2px;
  right: 5px;
}

.search-container {
